<template>
  <div class="my-music">
    <div class="music-item">
      <ul>
        <li>
          <div class="item-icon"><i class="icon-music">&#xe680;</i></div>
          <div class="item-content">我的音乐<span>({{myMusic}})</span></div>
          <div class="item-status"><i class="icon-plaing">&#xe651;</i></div>
        </li>
        <li>
          <div class="item-icon"><i class="icon-music">&#xe61c;</i></div>
          <div class="item-content">最近播放<span>({{history}})</span></div>
          <div class="item-status"></div>
        </li>
        <li>
          <div class="item-icon"><i class="icon-music">&#xe606;</i></div>
          <div class="item-content">我的电台<span>({{dianTai}})</span></div>
          <div class="item-status"></div>
        </li>
        <li>
          <div class="item-icon"><i class="icon-music">&#xe78c;</i></div>
          <div class="item-content">我的收藏<span>({{collect}})</span></div>
          <div class="item-status"></div>
        </li>
      </ul>
    </div>
    <div class="song-sheet">
      <header
        @click="open(0)">
        <i class="icon-arrow">&#xe60c;</i>
        我创建的歌单
        <i class="icon-set">&#xe65b;</i>
      </header>
      <transition name="hide">
        <ul v-show="songSheet">
          <li
            v-for="(item, i) in musicPic"
            @click="$router.push({ name: 'songList', params: { pic: item }})">
            <div class="sheet-pic">
              <img :src="item">
            </div>
            <div class="sheet-container">
              <div class="sheet-content">
                <div>我喜欢的音乐</div>
                <div>30首歌曲</div>
              </div>
              <div class="sheet-icon">
                <i class="icon-more">&#xe610;</i>
              </div>
            </div>
          </li>
        </ul>
      </transition>
    </div>
    <div class="song-sheet">
      <header
        @click="open(1)">
        <i class="icon-arrow">&#xe60c;</i>
        我收藏的歌单
        <i class="icon-set">&#xe65b;</i>
      </header>
      <transition name="hide">
        <ul v-show="songSheet1">
          <li
            v-for="(item, i) in musicPic" @click="$router.push({ name: 'songList', params: { pic: item }})">
            <div class="sheet-pic">
              <img :src="item">
            </div>
            <div class="sheet-container">
              <div class="sheet-content">
                <div>我收藏的音乐</div>
                <div>56首歌曲</div>
              </div>
              <div class="sheet-icon">
                <i class="icon-more">&#xe610;</i>
              </div>
            </div>
          </li>
        </ul>
      </transition>
    </div>
    <musicPlaying></musicPlaying>
  </div>
</template>

<script>
  import musicPlaying from '@/components/public/musicPlaying.vue';

  export default {
    name: "my-music",
    components: {
      musicPlaying
    },
    data() {
      return {
        myMusic: 28,
        history: 18,
        dianTai: 2,
        collect: 8,
        songSheet: false,
        songSheet1: true,
        musicPic: [
          "http://oiq8j9er1.bkt.clouddn.com/music_far%20away.jpg",
          "http://oiq8j9er1.bkt.clouddn.com/music_owl_city.jpg",
          "http://oiq8j9er1.bkt.clouddn.com/music_faded-1.jpg",
          "http://oiq8j9er1.bkt.clouddn.com/music_%E6%88%91%E8%BF%98%E6%83%B3%E5%A5%B9.jpg"
        ],
        angle: [
          '0deg',
          '0deg'
        ]
      }
    },
    methods: {
      open(index) {
        if (index === 0) {
          this.songSheet = !this.songSheet;
        }
        else {
          this.songSheet1 = !this.songSheet1;
        }
        this.angle[index] = this.angle[index] === '-90deg' ? '0deg' : '-90deg';
        document.getElementsByClassName('icon-arrow')[index].style.transform = "rotate(" + this.angle[index] + ")";
      }
    }
  }
</script>

<style scoped>
  .my-music {
    padding-bottom: 2.5rem;
  }

  .music-item li {
    height: 2.5rem;
    line-height: 2.5rem;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
    margin-bottom: 2px;
  }

  .music-item li > div {
    text-align: center;
    float: left;
  }

  .item-icon {
    width: 12%;
    margin-right: 1%;
  }

  .item-content {
    font-size: 14px;
    width: 73%;
    text-align: left !important;
  }

  .item-content span {
    color: #929292;
  }

  .item-status {
    width: 14%;
  }

  .icon-music {
    color: #c62f2f;
  }

  .icon-plaing {
    font-weight: bolder;
    color: #c62f2f;
  }

  .song-sheet > header {
    height: 1.3rem;
    line-height: 1.3rem;
    color: #5f5f5f;
    font-size: 12px;
    border-bottom: 1px solid #fff;
    background-color: #dedede;
  }

  .song-sheet li {
    height: 3rem;
    overflow: hidden;
  }

  .song-sheet li > div {
    height: 3rem;
    line-height: 3rem;
    float: left;
  }

  .sheet-container {
    height: 3rem !important;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
  }

  .sheet-container > div {
    height: 3rem;
    float: left;
  }

  .icon-arrow {
    display: inline-block;
    margin: 0 .5rem;
    transition-duration: .2s;
  }

  .icon-set {
    margin-right: 1rem;
    float: right;
  }

  .sheet-pic {
    padding: 5px;
    box-sizing: border-box;
    width: 3rem;
    border: 0;
  }

  .sheet-pic > img {
    width: 100%;
    height: 100%;
  }

  .sheet-content > div {
    width: 14.1rem;
    font-size: 12px;
    line-height: 2.2rem;
    height: 30%;
  }

  .icon-more {
    display: block;
    font-weight: bolder;
    transform: rotate(90deg);
  }

  .hide-enter-to, .hide-leave-to {
    overflow: hidden;
    transition: all .3s;
  }

  .hide-enter, .hide-leave-to {
    opacity: 0;
  }
</style>



